.container-font > .title-sistem > h1{
   position: relative;
   bottom: 0.1em;
   transition: all 3.5s ease-in-out;
   animation: 3.5s animaText  ;
   -webkit-animation: 3.5s animaText  ;
   -webkit-transition: all 3.5s ease-in-out;
   -moz-transition: all 3.5s ease-in-out;
   -ms-transition: all 3.5s ease-in-out;
   -o-transition: all 3.5s ease-in-out;
}

@-webkit-keyframes animaText{
  0%{
    color: rgba(0, 0, 0, 0.342);
    position: relative;
    bottom: 0.9em;
  }
  100%{
    color: white;
    bottom: 0.1;
  }
}

@keyframes animaText{
  0%{
    color: rgba(0, 0, 0, 0.342);
    position: relative;
    bottom: 0.9em;
  }
  100%{
    color: white;
    bottom: 0.1;
  }
}

